@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: local("Montserrat"), url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: local("Montserrat"), url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: local("Montserrat"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: local("Montserrat"), url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.page {
  height: 100%;
  min-width: 320px;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.4;
  font-weight: 400;
  color: #333333;
  background-color: #ffffff;
}

.title-secondary {
  font-size: 24px;
  line-height: 1.5;
  font-weight: 700;
}
@media (min-width: 768px) {
  .title-secondary {
    font-size: 42px;
    line-height: 1.5;
  }
}

.title-tertiary {
  font-size: 18px;
  line-height: 1.8;
  font-weight: 700;
}
@media (min-width: 768px) {
  .title-tertiary {
    font-size: 24px;
    line-height: 1.5;
  }
}

.title-quaternary {
  font-size: 14px;
  line-height: 1.4;
  font-weight: 700;
  color: #7859cf;
}

.logo {
  display: block;
}
@media (min-width: 1440px) {
  .logo--header {
    display: flex;
    align-items: center;
  }
}

.logo--header .logo__image {
  width: 32px;
  height: 36px;
  background-image: url("../img/logos/logo-phone.svg");
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .logo--header .logo__image {
    width: 82px;
    background-image: url("../img/logos/logo-tablet.svg");
  }
}
@media (min-width: 1440px) {
  .logo--header .logo__image {
    width: 170px;
    background-image: url("../img/logos/logo-desktop.svg");
  }
}

.logo--footer .logo__image {
  width: 82px;
  height: 36px;
  background-image: url("../img/logos/logo-tablet.svg");
}
@media (min-width: 768px) {
  .logo--footer .logo__image {
    width: 170px;
    background-image: url("../img/logos/logo-desktop.svg");
  }
}

@media (max-width: 1439px) {
  .logo--header .logo__title,
.logo--footer .logo__title {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
@media (min-width: 1440px) {
  .logo--header .logo__title,
.logo--footer .logo__title {
    width: 126px;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 20px;
    font-weight: 400;
    font-size: 12px;
    line-height: 1.3;
    color: #333333;
    text-decoration: none;
  }
}

@media (min-width: 1440px) {
  .logo--footer .logo__title {
    width: 100%;
    margin-top: 10px;
    margin-left: 0;
  }
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  border: none;
  text-decoration: none;
  background: none;
  cursor: pointer;
  box-shadow: none;
  outline: none;
  box-sizing: border-box;
  padding: 8px 20px;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 400;
  color: #ffffff;
  background-color: #7859cf;
  border-radius: 4px;
}
.button--product-card-buy {
  position: relative;
  padding-left: 46px;
  font-size: 14px;
  line-height: 1.4;
}
.button--product-card-buy::before {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 20px;
  margin: auto 0;
  width: 20px;
  height: 16px;
  content: "";
  background-image: url("../../img/icons/button-cart.svg");
}
.button--filter, .button--reset-filter {
  min-width: 160px;
  font-size: 13px;
  line-height: 1.8;
  padding: 10px 3px;
}
@media (min-width: 768px) {
  .button--filter, .button--reset-filter {
    min-width: 148px;
    padding: 8px 3px;
    font-size: 14px;
    line-height: 1.7;
  }
}
.button--reset-filter {
  color: #7859cf;
  background-color: #ffffff;
}
.button--reset-filter:hover, .button--reset-filter:focus {
  color: #ffffff;
}
.button--reset-filter.button--disabled {
  color: #ffffff;
  background-color: #e2e2e2;
  cursor: default;
}
.button--reset-filter.button--disabled:hover, .button--reset-filter.button--disabled:focus, .button--reset-filter.button--disabled:active {
  background-color: #e2e2e2;
}
.button:hover, .button:focus {
  background-color: #9070ec;
}
.button:active {
  background-color: #593cac;
}
.button--disabled {
  background-color: #bdbdbd;
  cursor: default;
}
.button--disabled:hover, .button--disabled:focus, .button--disabled:active {
  background-color: #bdbdbd;
}

.badge {
  font-size: 12px;
  line-height: 1.3;
  letter-spacing: 0.04em;
  font-weight: 700;
  color: #7859cf;
  text-transform: uppercase;
}

.price {
  font-size: 20px;
  line-height: 1.3;
  font-weight: 600;
}
.price--old {
  font-size: 16px;
  line-height: 2;
  color: #7859cf;
  text-decoration: line-through;
}

.select {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 16px;
  line-height: 1.5;
  text-transform: lowercase;
}

.select__selected-field {
  position: relative;
  overflow: hidden;
  padding: 4px 30px 4px 10px;
  border-radius: 4px;
  font-weight: 600;
  color: #7859cf;
  white-space: nowrap;
  cursor: pointer;
}
.select__selected-field::after {
  position: absolute;
  z-index: 2;
  top: -2px;
  right: 10px;
  bottom: 0;
  width: 10px;
  height: 10px;
  margin: auto 0;
  content: "";
  background-image: url("../../img/icons/pagination-arrow-left.svg");
  background-repeat: no-repeat;
  transform: rotate(-90deg);
  pointer-events: none;
}
.select__selected-field--opened::after {
  transform: translateY(4px) rotate(90deg);
}
.select__selected-field--opened + .select__list {
  visibility: visible;
}
.select__selected-field:hover {
  background-color: #f0ebff;
}
.select__selected-field:focus {
  outline: none;
}
.select__selected-field p {
  overflow: hidden;
  margin-top: 0;
  margin-bottom: 0;
}

.select__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
  position: absolute;
  right: 0;
  bottom: -10px;
  visibility: hidden;
  overflow: hidden;
  background-color: #ffffff;
  border-radius: 4px;
  transform: translateY(100%);
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.2);
}

.select__item {
  padding: 8px 20px;
  text-transform: lowercase;
  cursor: pointer;
}
.select__item:not(:last-child) {
  border-bottom: 1px solid #f0ebff;
}
.select__item:hover:not(.select__item--current), .select__item:focus:not(.select__item--current) {
  background-color: #f0ebff;
  outline: none;
}
.select__item--current {
  color: #ffffff;
  background-color: #7859cf;
}
.select__item--current:focus {
  outline: none;
}

.pagination {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 20px;
  justify-content: center;
  align-items: center;
}
.pagination--begin .pagination__link--back {
  visibility: hidden;
}
.pagination--end .pagination__link--forward {
  visibility: hidden;
}
.pagination--begin::before, .pagination--end::after {
  width: 40px;
  height: 40px;
  content: "";
}
@media (min-width: 768px) {
  .pagination {
    grid-gap: 0;
  }
  .pagination--begin::before {
    width: 91px;
  }
  .pagination--end::after {
    width: 104px;
  }
}

.pagination__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 20px;
  justify-content: center;
}

.pagination__link {
  display: flex;
  justify-content: center;
  align-content: center;
  box-sizing: border-box;
  min-width: 40px;
  padding: 8px;
  font-size: 16px;
  line-height: 1.5;
  color: #7859cf;
  text-decoration: none;
  background-color: #ffffff;
  border-radius: 4px;
}
.pagination__link--current {
  color: #333333;
  font-weight: 600;
  background-color: #f8f8f8;
}
.pagination__link--arrow {
  padding: 15px;
}
.pagination__link--forward .pagination__link-icon {
  transform: rotate(180deg);
}
.pagination__link:hover:not(.pagination__link--current:hover, .pagination__link--disabled:hover), .pagination__link:focus:not(.pagination__link--current:focus, .pagination__link--disabled:focus) {
  color: #ffffff;
  background-color: #9070ec;
  outline: none;
}
.pagination__link:active:not(.pagination__link--current:active, .pagination__link--disabled:active) {
  color: #ffffff;
  background-color: #593cac;
  outline: none;
}
.pagination__link--arrow:hover .pagination__link-icon, .pagination__link--arrow:focus .pagination__link-icon, .pagination__link--arrow:active .pagination__link-icon {
  fill: #ffffff;
}
@media (min-width: 768px) {
  .pagination__link--arrow .pagination__link-icon {
    display: none;
  }
  .pagination__link--back {
    padding: 8px 43px 8px 52px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  .pagination__link--forward {
    padding: 8px 47px 8px 44px;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
  .pagination__link--first-number {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
  .pagination__link--last-number {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
}

.pagination__link-label {
  display: none;
}
@media (min-width: 768px) {
  .pagination__link-label {
    display: block;
  }
}

.product-card {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-rows: repeat(4, auto);
  width: 280px;
  box-sizing: border-box;
  padding: 20px;
  padding-top: 40px;
  background-color: #ffffff;
  border-radius: 4px;
}
@media (min-width: 768px) {
  .product-card {
    width: 350px;
    padding: 30px;
    padding-top: 50px;
  }
}
@media (min-width: 1440px) {
  .product-card {
    width: 318px;
    cursor: pointer;
  }
  .product-card:hover, .product-card:focus {
    outline: none;
    box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.15);
  }
}

.product-card__title {
  grid-column: 1/3;
  grid-row: 2/3;
  justify-self: start;
  margin-top: 0;
  margin-bottom: 4px;
}
.product-card__title a {
  color: #333333;
  text-decoration: none;
}
@media (min-width: 768px) {
  .product-card__title {
    margin-bottom: 10px;
  }
}

.product-card__image-link {
  grid-column: 1/3;
  grid-row: 1/2;
  justify-self: center;
  margin-bottom: 24px;
}
@media (min-width: 768px) {
  .product-card__image-link {
    margin-bottom: 30px;
  }
}

.product-card__image {
  width: 130px;
  height: 188px;
}

.product-card__description {
  grid-column: 1/3;
  grid-row: 3/4;
  justify-self: start;
  margin-top: 0;
  margin-bottom: 16px;
}
@media (min-width: 768px) {
  .product-card__description {
    margin-bottom: 20px;
  }
}

.product-card__price {
  grid-column: 1/2;
  grid-row: 4/5;
  justify-self: start;
  align-self: center;
  margin-top: 0;
  margin-bottom: 0;
}

.product-card__order-button {
  grid-column: 2/3;
  grid-row: 4/5;
  justify-self: end;
  align-self: center;
  min-width: 141px;
}

.main__map {
  height: 420px;
}
@media (min-width: 768px) {
  .main__map {
    height: 480px;
  }
}
@media (min-width: 1440px) {
  .main__map {
    height: 540px;
  }
}

.header {
  box-sizing: border-box;
  padding-right: 0;
  padding-left: 0;
}
@media (min-width: 768px) {
  .header {
    padding-right: 20px;
    padding-left: 20px;
  }
}
@media (min-width: 1440px) {
  .header {
    padding-right: 40px;
    padding-left: 40px;
  }
}

@media (min-width: 768px) {
  .header__navigation-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
}
@media (min-width: 1440px) {
  .header__navigation-container {
    width: 1360px;
    margin: 0 auto;
  }
}

.header__logo {
  margin-top: 12px;
}
@media (min-width: 768px) {
  .header__logo {
    margin-top: 22px;
  }
}

@media (min-width: 768px) {
  .header__navigation {
    display: flex;
  }
}

.header__navigation-link {
  font-size: 12px;
  line-height: 1.3;
  color: #333333;
  text-decoration: none;
  display: block;
  padding: 20px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.02em;
  text-align: center;
  border-bottom: 1px solid #f0ebff;
}
.header__navigation-link:first-child {
  border-top: 1px solid #f0ebff;
}
.header__navigation-link:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .header__navigation-link {
    border: none;
    padding: 32px 20px;
  }
  .header__navigation-link:first-child {
    border: none;
  }
  .header__navigation-link--current {
    position: relative;
    color: #7859cf;
  }
  .header__navigation-link--current::after {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    content: "";
    border-bottom: 2px solid #7859cf;
  }
  .header__navigation-link:hover, .header__navigation-link:focus, .header__navigation-link:active {
    color: #7859cf;
    outline: none;
  }
}

.header__user-navigation {
  display: flex;
}

.header__user-link {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 60px;
  min-height: 60px;
  text-decoration: none;
}
.header__user-link:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .header__user-link {
    min-height: 80px;
  }
  .header__user-link:hover, .header__user-link:focus, .header__user-link:active {
    outline: none;
  }
  .header__user-link:hover .header__user-link-title, .header__user-link:focus .header__user-link-title, .header__user-link:active .header__user-link-title {
    color: #7859cf;
  }
  .header__user-link:hover .header__user-link-icon, .header__user-link:focus .header__user-link-icon, .header__user-link:active .header__user-link-icon {
    fill: #7859cf;
  }
}
@media (min-width: 1440px) {
  .header__user-link {
    margin-right: 20px;
    padding-right: 20px;
    padding-left: 20px;
  }
  .header__user-link:last-child {
    margin-right: 0;
  }
}

@media (min-width: 1440px) {
  .header__user-link-icon {
    margin-right: 8px;
  }
}

.header__user-link-title {
  display: none;
}
@media (min-width: 1440px) {
  .header__user-link-title {
    font-size: 12px;
    line-height: 1.3;
    color: #333333;
    text-decoration: none;
    display: block;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.02em;
  }
}

.header__navigation-toggle {
  position: relative;
  box-sizing: border-box;
  width: 60px;
  height: 60px;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
.header__navigation-toggle:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .header__navigation-toggle {
    display: none;
  }
}

.header__navigation-container--opened .header__navigation-toggle::before,
.header__navigation-container--opened .header__navigation-toggle::after {
  position: absolute;
  top: 22px;
  right: 0;
  bottom: 22px;
  left: 0;
  width: 1.5px;
  margin: auto;
  content: "";
  background-color: #333333;
  transform: rotate(45deg);
}

.header__navigation-container--opened .header__navigation-toggle::after {
  transform: rotate(-45deg);
}

.header__navigation-container--closed .header__navigation-toggle::before {
  position: absolute;
  z-index: 10;
  top: 25px;
  right: 22px;
  left: 22px;
  height: 1.5px;
  content: "";
  background-color: #000000;
  box-shadow: 0 4.5px 0 0 #000000, 0 9.5px 0 0 #000000;
}

@media (max-width: 767px) {
  .header__navigation-container {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding-left: 20px;
  }

  .header__user-navigation {
    margin-left: auto;
  }

  .header__navigation-container--closed .header__navigation {
    display: none;
  }

  .header__navigation-container--opened .header__navigation {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10;
    display: block;
    background-color: #ffffff;
    transform: translateY(100%);
  }

  .header__navigation-container--nojs {
    display: grid;
    grid-template-rows: repeat(2, auto);
    grid-template-columns: 20px repeat(2, auto) 20px;
    justify-content: normal;
    padding-left: 0;
    padding-right: 0;
  }

  .header__navigation-container--nojs .header__navigation-toggle {
    display: none;
  }

  .header__navigation-container--nojs .header__logo {
    grid-row: 1/2;
    grid-column: 2/3;
  }

  .header__navigation-container--nojs .header__navigation {
    display: block;
    grid-row: 2/3;
    grid-column: 1/-1;
  }

  .header__navigation-container--nojs .header__user-navigation {
    grid-row: 1/2;
    grid-column: -2/-3;
    margin-left: auto;
  }
}
.footer {
  box-sizing: border-box;
  padding-right: 0;
  padding-left: 0;
}
@media (min-width: 768px) {
  .footer {
    padding-right: 20px;
    padding-left: 20px;
  }
}
@media (min-width: 1440px) {
  .footer {
    padding-right: 40px;
    padding-left: 40px;
  }
}

@media (min-width: 768px) {
  .footer__wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, auto);
    column-gap: 30px;
  }
}
@media (min-width: 1440px) {
  .footer__wrapper {
    margin: 0 auto;
    width: 1360px;
    grid-template-columns: 318px repeat(3, 1fr);
    grid-template-rows: repeat(2, auto);
  }
