.footer__social {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}
@media (min-width: 768px) {
  .footer__social {
    grid-column: 1/-1;
    grid-row: 1/2;
    padding: 42px 0 32px;
  }
}
@media (min-width: 1440px) {
  .footer__social {
    grid-column: 1/2;
    display: block;
    padding: 40px 0 20px;
  }
}

.footer__logo {
  display: block;
  margin-top: 2px;
}
@media (min-width: 1440px) {
  .footer__logo {
    margin-bottom: 50px;
  }
}

@media (min-width: 1440px) {
  .footer__logo-image {
    margin-bottom: 10px;
  }
}

.footer__logo-title {
  display: none;
}
@media (min-width: 1440px) {
  .footer__logo-title {
    display: block;
  }
}

.footer__social-list {
  display: flex;
  flex-wrap: wrap;
}

.footer__social-link {
  width: 20px;
  height: 20px;
  margin-left: 20px;
  padding: 10px;
  text-decoration: none;
  border-radius: 4px;
}
.footer__social-link--pinterest {
  width: 22px;
  height: 22px;
  padding: 9px;
}
.footer__social-link:hover, .footer__social-link:focus {
  background-color: #f0ebff;
}
.footer__social-link:active {
  background-color: rgba(120, 89, 207, 0.25);
