    grid-column: 2/3;
    grid-template-columns: repeat(3, auto);
    margin-top: 60px;
  }
}

.advantages {
  box-sizing: border-box;
  padding: 50px 20px 60px;
}
@media (min-width: 768px) {
  .advantages {
    padding-top: 60px;
    padding-bottom: 80px;
  }
}
@media (min-width: 1440px) {
  .advantages {
    padding: 100px 40px 120px;
  }
}

.advantages__wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}
@media (min-width: 1440px) {
  .advantages__wrapper {
    width: 1360px;
  }
}

.advantages__title,
.advantages__badge {
  text-align: center;
}

.advantages__title {
  order: 2;
  margin-top: 0;
  margin-bottom: 36px;
}
@media (min-width: 768px) {
  .advantages__title {
    margin-bottom: 60px;
  }
}
@media (min-width: 1440px) {
  .advantages__title {
    margin-bottom: 80px;
  }
}

.advantages__badge {
  order: 1;
  margin-top: 0;
  margin-bottom: 4px;
}

.advantages__list {
  order: 3;
  display: grid;
  grid-template-columns: minmax(280px, 360px);
  row-gap: 32px;
  margin: 0 auto;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .advantages__list {
    grid-template-columns: repeat(2, minmax(340px, 440px));
    column-gap: 30px;
    row-gap: 50px;
  }
}
@media (min-width: 1440px) {
  .advantages__list {
    grid-template-columns: repeat(4, 1fr);
  }
}

.advantages__item {
  display: grid;
  grid-template-rows: 66px repeat(2, auto);
  align-items: start;
}
.advantages__item::before {
  justify-self: center;
  width: 52px;
  height: 56px;
  content: "";
  background-repeat: no-repeat;
}
.advantages__item--speed::before {
  background-image: url("../img/icons/advantage-speed.svg");
}
.advantages__item--convenience::before {
  background-image: url("../img/icons/advantage-convenience.svg");
}
.advantages__item--cheerfulness::before {
  background-image: url("../img/icons/advantage-cheerfulness.svg");
}
.advantages__item--ecology::before {
  background-image: url("../img/icons/advantage-ecology.svg");
}
@media (min-width: 768px) {
  .advantages__item {
    grid-template-rows: 46px auto;
    grid-template-columns: 72px auto;
  }
  .advantages__item::before {
    grid-row: 1/3;
    grid-column: 1/2;
    margin-right: 10px;
  }
}
@media (min-width: 1440px) {
  .advantages__item {
    grid-template-rows: 76px repeat(2, auto);
    grid-template-columns: auto;
  }
  .advantages__item::before {
    grid-row: 1/2;
    grid-column: 1/2;
    margin-right: 0;
  }
}

.advantages__item-title {
  grid-row: 2/3;
  margin-top: 0;
  margin-bottom: 4px;
  text-align: center;
}
@media (min-width: 768px) {
  .advantages__item-title {
    grid-row: 1/2;
    grid-column: 2/3;
    margin-bottom: 10px;
    text-align: left;
  }
}
@media (min-width: 1440px) {
  .advantages__item-title {
    grid-row: 2/3;
    grid-column: 1/2;
    text-align: center;
  }
}

.advantages__item-description {
  grid-row: 3/4;
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .advantages__item-description {
    grid-row: 2/3;
    grid-column: 2/3;
    font-size: 16px;
    line-height: 1.5;
    text-align: left;
  }
}
@media (min-width: 1440px) {
  .advantages__item-description {
    grid-row: 3/4;
    grid-column: 1/2;
    text-align: center;
  }
}

.map {
  height: 100%;
}
.map img {
  display: block;
  width: 320px;
  height: 420px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .map img {
    width: 768px;
    height: 480px;
  }
}
@media (min-width: 1440px) {
  .map img {
    width: 1440px;
    height: 540px;
  }
}